<template>
  <v-container fluid class="bg">
    <v-row>
      <v-col cols="1"></v-col>
      <v-col cols="7">
        <div class="space-head">
          <h1>SLOT MACHINE</h1>
          <h1>GACHAPON</h1>
        </div>
        <div class="space">
          <v-btn @click="goToGacha" x-large color="error" dark>
            <span>START NOW</span>
          </v-btn>
        </div>
        <div class="space">
          <!-- login form -->
          <v-dialog v-model="loginForm" max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn x-large color="error" dark v-bind="attrs" v-on="on">
                <span>PLAY WITH <br />ACCOUNT</span>
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">Login</span>
                <v-spacer></v-spacer>
                <v-btn icon @click="loginForm = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        label="username"
                        required
                        v-model="usernameLogin"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        label="Password*"
                        type="password"
                        required
                        v-model="passwordLogin"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-btn color="blue darken-1" text @click="log"> log in </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="(signUpForm = true), (loginForm = false)"
                >
                  new player? sign up
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <!-- sign up form -->
          <v-dialog v-model="signUpForm" max-width="600px">
            <v-card>
              <v-card-title>
                <span class="text-h5">Sign Up</span>
                <v-spacer></v-spacer>
                <v-btn icon @click="signUpForm = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        label="username"
                        required
                        v-model="usernameSignUp"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        label="Password*"
                        type="password"
                        required
                        v-model="passwordSignUp"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-btn color="blue darken-1" text @click="logUp">
                  sign up
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import { response } from 'express';
// import gachaponService from "../service"
export default {
  name : "Login-page",
  data: () => ({
    loginForm: false,
    signUpForm: false,
    usernameLogin: "",
    passwordLogin: "",
    usernameSignUp: "",
    passwordSignUp: "",
  }),
  methods: {
    goToGacha() {
      return this.$router.push("/gachapon");
    },
    log() {
      if (this.usernameLogin != "" && this.passwordLogin != "") {
        // var loginData = { username: this.usernameLogin}
        // gachaponService.checkUser(this.this.usernameLogin)
        // .then(response => {

        // })
        this.loginForm = false;
        // test = 1
        console.log("name " + this.usernameLogin);
        console.log("pass " + this.passwordLogin);
        return this.$router.push("/gachapon");
      }
    },
    logUp() {
      if (this.usernameSignUp != "" && this.passwordSignUp != "") {
        this.signUpForm = false;
        this.loginForm = true;
        console.log("name " + this.usernameSignUp);
        console.log("pass " + this.passwordSignUp);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.bg {
  background-image: url("../assets/login_bg.svg");
  height: 100%;
  width: 100%;
  background-size: contain;
  background-position: right center;
}
h1 {
  font-size: 74px;
  color: white;
}
span {
  font-size: 20px;
}
.v-btn {
  width: 200px;
  height: 200px;
  cursor: pointer;
}
.space {
  padding-bottom: 50px;
}
.space-head {
  padding-top: 50px;
  padding-bottom: 50px;
}
</style>
