<template>
  <div>
    <v-app-bar color="#4f4858" elevation="0">
      <v-btn @click="goToLogin" large color="#F66767">EXIT</v-btn>
      <v-spacer> </v-spacer>
      <v-dialog v-model="dialog" width="600">
        <template v-slot:activator="{ on, attrs }">
          <v-btn large color="#FFBD59" v-bind="attrs" v-on="on">RATE</v-btn>
        </template>
        <v-img :src="require('../assets/rate_gacha.svg')" />
      </v-dialog>
    </v-app-bar>
    <!-- bar -->
    <v-container fluid>
      <v-dialog v-model="coinNotEnough" width="600">
        <v-img :src="require('../assets/out_coin.svg')" />
      </v-dialog>
      <!-- out for coin modal -->
      <v-dialog v-model="received" width="600">
        <v-card align="center">
          <v-img :src="require('../assets/modalBG.svg')">
            <h1 style="color: red">CONGRATULATION !<br />YOU HAVE RECEIVED</h1>
            <v-img :src="itemImage" max-width="300"></v-img>
            <h2>{{ item }}</h2>
            <v-btn @click="(inventoryPopUp = true), (received = false)"
              >to inven</v-btn
            >
          </v-img>
        </v-card>
      </v-dialog>
      <!-- received card -->
      <v-dialog v-model="inventoryPopUp" width="600">
        <v-card>
          <v-img :src="require('../assets/modalBG.svg')">
            <h1>inventory</h1>
            <v-row v-for="item in inventory" :key="item.id">
              <v-col cols="2">
                <v-img :src="item.img" max-width="50"></v-img>
              </v-col>
              <v-col>
                <b>{{ item.itemName }}</b> amount : {{ item.amount }}
              </v-col>
              <hr />
            </v-row>
          </v-img>
        </v-card>
      </v-dialog>
      <!-- inventory -->
      <v-row>
        <v-col cols="3" class="col">
          <v-img
            :src="require('../assets/spin.svg')"
            width="250"
            class="contain"
          />
        </v-col>
        <v-col cols="6" align="center">
          <v-img
            :src="slot"
            width="420"
            @click="random(remainingCoin)"
            style="cursor: pointer; position: relative; z-index: 1"
          />
          <!-- :src="require('../assets/slot/common.svg')" -->
          <v-img
            :src="slotImg"
            width="240"
            style="top: 48%; position: absolute; left: 32%"
          >
          </v-img>
        </v-col>
        <v-col cols="3" class="col">
          <div class="contain">
            <div class="box">
              <v-img :src="require('../assets/coin.png')" max-width="75" />
              <h1>remaining : {{ remainingCoin }}</h1>
            </div>
            <br />
            <v-img
              :src="require('../assets/inventory.svg')"
              width="320"
              @click="inventoryPopUp = true"
              style="cursor: pointer"
            />
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import RateGacha from "@/components/RateGacha.vue";
// import OutCoin from "@/components/OutOfCoin.vue";

export default {
  name: "PlaySection",
  props:["test"],
  data() {
    return {
      dialog: false,
      remainingCoin: 1000,
      coinNotEnough: false,
      received: false,
      slot: require("../assets/w_common.svg"),
      slotImg: require("../assets/slot/common.svg"),
      item: "",
      itemImage: "",
      inventory: [],
      inventoryPopUp: false,
    };
  },
  components: {
    // HelloWorld,
    // RateGacha,
    // OutCoin
  },
  methods: {
    goToLogin() {
      return this.$router.push("/");
    },

    random(coin) {
      if (coin < 49) {
        this.coinNotEnough = true;
        return;
      }
      // console.log(test);
      let rate = Math.floor(Math.random() * 100);
      let rateSlot;
      let show = [
        require("../assets/slot/common.svg"),
        require("../assets/slot/rare.svg"),
        require("../assets/slot/ultraRare.svg"),
      ];
      let i_common = [
        { id: 1, name: "Coin+20", img: require("../assets/reward/coin20.png") },
        { id: 2, name: "Hat", img: require("../assets/reward/hat.png") },
        { id: 3, name: "T-shirt", img: require("../assets/reward/shirt.png") },
      ];
      let i_rare = [
        { id: 4, name: "Coin+50", img: require("../assets/reward/coin50.png") },
        { id: 5, name: "Hoddie", img: require("../assets/reward/hoddie.png") },
        {
          id: 6,
          name: "(+) View Solution Quota",
          img: require("../assets/reward/solutionQuota.png"),
        },
        {
          id: 7,
          name: "Discount coupon 30% off (Monthly)",
          img: require("../assets/reward/dis30.png"),
        },
      ];
      let i_ultraRare = [
        {
          id: 8,
          name: "Coin+100",
          img: require("../assets/reward/coin100.png"),
        },
        {
          id: 9,
          name: "(+) Exam Quota",
          img: require("../assets/reward/examQuota.png"),
        },
        {
          id: 10,
          name: "Discount coupon 50% off (Yearly)",
          img: require("../assets/reward/dis50.png"),
        },
      ];

      this.remainingCoin -= 50;
      if (rate < 51) {
        let item = Math.floor(Math.random() * i_common.length);
        rateSlot = 0;
        if (item == 0) {
          this.remainingCoin += 20;
        }
        this.slot = require("../assets/w_common.svg");
        this.item = "Common item : " + i_common[item].name;
        this.itemImage = i_common[item].img;
        this.slotImg = show[rateSlot];
      } else if (rate < 80) {
        let item = Math.floor(Math.random() * i_rare.length);
        rateSlot = 1;
        if (item == 0) {
          this.remainingCoin += 50;
        }
        this.slot = require("../assets/w_rare.svg");
        this.item = "Rare item : " + i_rare[item].name;
        this.itemImage = i_rare[item].img;
        this.slotImg = show[rateSlot];
      } else {
        let item = Math.floor(Math.random() * i_ultraRare.length);
        rateSlot = 2;
        if (item == 0) {
          this.remainingCoin += 100;
        }
        this.slot = require("../assets/w_ultra.svg");
        this.item = "UltraRare item : " + i_ultraRare[item].name;
        this.itemImage = i_ultraRare[item].img;
        this.slotImg = show[rateSlot];
      }
      setTimeout(() => (this.received = true), 500);
      const result = this.inventory.find(
        ({ itemName }) => itemName === this.item
      );
      if (result) {
        result.amount += 1;
      } else {
        this.inventory.push({
          itemName: this.item,
          amount: 1,
          img: this.itemImage,
        });
      }
    },
  },
};
</script>

<style scoped>
.col {
  position: relative;
}

.contain {
  position: absolute;
  bottom: 10%;
}
.box {
  background-color: #d9d9d9;
  width: 320px;
  height: 100px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
